/*eslint-disable*/module.exports={messages:{"Amount":"총합계","Appearance":"외관","Back":"뒤로","Can be backed up to mnemonic seed":"니모닉 시드에 백업할 수 있습니다","Cancel":"취소","Connected":"연결됨","Copied":"복사됨","Copy to Clipboard":"클립보드에 복사","Create a new private key":"새로운 개인 키 만들기","DANGER: permanently delete private key":"경고: 영구적으로 개인 키를 삭제합니다.","Dark":"어두운 테마","Delete":"삭제","Delete all keys":"모든 키 삭제","Delete key {fingerprint}":["보호키 삭제 ",["fingerprint"]],"Deleting all keys will permanently remove the keys from your computer, make sure you have backups. Are you sure you want to continue?":"모든 키를 컴퓨터에서 영구적으로 제거합니다. 만일을 대비하여 백업이 준비되었는지 확인하십시오. 계속하시겠습니까?","Deleting the key will permanently remove the key from your computer, make sure you have backups. Are you sure you want to continue?":"이 키를 컴퓨터에서 영구적으로 삭제합니다. 만일을 대비하여 백업이 되어 있는지 확인하십시오. 계속하시겠습니까?","Disable":"사용 안함","Disable Passphrase Protection":"암호 보호 비활성화","Discard":"디스코드","Error":"오류","Error:":"오류:","FAQ":"자주하는 질문","Farmer public key:":"농부 퍼블릭 키","Farming":"재배 중","Fee":"수수료","Frequently Asked Questions":"자주 묻는 질문","Help":"도움말","Help translate":"번역 돕기","Hide Advanced Options":"고급 옵션 숨기기","Import from Mnemonics (24 words)":"니모닉에서 불러오기 (24단어)","Incorrect value":"올바르지 않은 값","Language":"언어설정","Light":"밝은 테마","Loading list of the keys":"키 목록 로드 중","Log Out":"로그아웃","Logout":"로그아웃","Migrate":"마이그레이션","Migration required":"마이그레이션 필요함","Mode":"모드","New passphrase is the same as your current passphrase":"새 암호가 현재 암호와 동일합니다.","No 24 word seed, since this key is imported.":"24 단어의 씨드가 없습니다, 나중에 키를 가져 옵니다.","Not connected":"연결되지 않음","OK":"확인","Pool public key:":"풀 퍼블릭 키","Private key with public fingerprint {fingerprint}":["퍼블릭 핑거프린트 ",["fingerprint"]," 의 개인 키"],"Private key {fingerprint}":["개인 키 ",["fingerprint"]],"Private key:":"개인 키:","Public key:":"퍼블릭 키:","Reload Application":"애플리케이션 새로고침","Report an Issue":"문제 보고","See private key":"개인 키 보기","Seed:":"시드:","Select Key":"키 선택","Select...":"선택...","Send Feedback":"피드백 전송","Setting a passphrase is strongly recommended to protect your keys. Are you sure you want to skip setting a passphrase?":"키를 보호하려면 암호를 설정하는 것이 좋습니다. 암호 설정을 건너뛰시겠습니까?","Settings":"설정","Show Advanced Options":"고급 옵션 표시","Sign In":"로그인","Skip":"건너뛰기","Skip Passphrase Protection":"암호 보호 건너뛰기","Something went wrong":"문제가 발생함","The provided passphrase and confirmation do not match":"제공한 암호와 확인이 일치하지 않습니다","Transaction has not been sent to node yet":"거래가 아직 노드로 전송되지 않음","Transaction has sent to a full node and is pending inclusion into the mempool. {0}":["거래가 전체 노드로 전송되었으며 mempool에 포함되도록 보류 중입니다. ",["0"]],"Transaction has successfully been sent to a full node and included in the mempool.":"거래가 전체 노드로 성공적으로 전송되었고 mempool에 포함되었습니다.","Try Again":"다시 시도","Unable to load the list of the keys":"키 목록을 로드할 수 없습니다.","Unknown":"알 수 없음","Unsaved Changes":"변경 내용이 저장되지 않음","Using a passphrase is strongly recommended to protect your keys. Are you sure you want to disable passphrase protection?":"키를 보호하려면 암호를 사용하는 것이 좋습니다. 암호 보호를 비활성화하시겠습니까?","Value seems high":"값이 높음","Wallet":"내 지갑","Warning: This key is used for a wallet that may have a non-zero balance. By deleting this key you may lose access to this wallet":"경고: 이 키는 잔액이 있을수도 있는 지갑에 사용됩니다. 이 키를 삭제하면 이 지갑에 액세스하지 못할 수 있습니다.","Warning: This key is used for your farming rewards address. By deleting this key you may lose access to any future farming rewards":"경고: 이 키는 파밍 보상 주소에 사용됩니다. 이 키를 삭제하면 향후 파밍 보상에 액세스하지 못할 수 있습니다.","Warning: This key is used for your pool rewards address. By deleting this key you may lose access to any future pool rewards":"경고: 이 키는 풀 보상 주소에 사용됩니다. 이 키를 삭제하면 향후 풀 보상에 접근하지 못할 수 있습니다.","Welcome to Chia. Please log in with an existing key, or create a new key.":"Chia에 오신 것을 환영합니다. 사용하고 계신 키로 로그인하시거나 새로운 키를 만드세요.","You have made changes. Do you want to discard them?":"변경했습니다. 삭제하시겠습니까?","Your keys have not been migrated to a new keyring. You will be unable to create new keys or delete existing keys until migration completes. Would you like to migrate your keys now?":"키가 새 키링으로 마이그레이션되지 않았습니다. 마이그레이션이 완료될 때까지 새 키를 생성하거나 기존 키를 삭제할 수 없습니다. 지금 키를 마이그레이션하시겠습니까?","{0, plural, one {mojo} other {mojos}}":[["0","plural",{one:"mojo",other:"mojos"}]],"{minPassphraseLength, plural, one {Passphrases must be at least # character in length} other {Passphrases must be at least # characters in length}}":[["minPassphraseLength","plural",{one:["Passphrases must be at least ","#"," character in length"],other:["Passphrases must be at least ","#"," characters in length"]}]]}};